.exchange_card_container{
  display: flex;
  width: 99%;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: space-between;
}

.weitingCoin {
  margin: 0 auto;
  width: 100px;

  animation-name: coin_witing;
  animation-duration: 3s;
  animation-delay: 0.2s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}


@keyframes coin_witing {
  from {
    transform: rotate(360deg);
    opacity: 1;
  }
  to {
    opacity: 1;
  }
}
